
<template>
  <div class="footer">
    <van-tabbar
      v-model="active"
      @change="act"
      active-color="rgb(235, 97, 0)"
      inactive-color="#000"
    >
      <van-tabbar-item to="/index" text="首页" icon="home-o">
        <span>首页</span>
        <template #icon>
          <img
            :src="
              active == 0
                ? 'https://wap.365msmk.com/img/home-active.87af5fd4.png'
                : 'data:image/png;base64,'
            "
            alt=""
          />
        </template>
      </van-tabbar-item>
      <van-tabbar-item to="/course" icon="search">
        <span>课程</span>
        <template #icon>
          <img
            :src="
              active == 1
                ? 'https://wap.365msmk.com/img/course-active.cb51bc51.png'
                : 'data:image/png;base64,'
            "
            alt=""
          /> </template
      ></van-tabbar-item>
      <van-tabbar-item to="/record" icon="friends-o"
        ><span>约课记录</span>
        <template #icon>
          <img
            :src="
              active == 2
                ? 'https://wap.365msmk.com/img/study-active.a0a234d2.png'
                : 'data:image/png;base64,'
            "
            alt=""
          /> </template
      ></van-tabbar-item>
      <van-tabbar-item to="/practise" icon="setting-o"
        ><span>练习</span>
        <template #icon>
          <img
            :src="
              active == 3
                ? 'data:image/png;base64,'
                : 'data:image/png;base64,'
            "
            alt=""
          /> </template
      ></van-tabbar-item>
      <van-tabbar-item to="/person" icon="setting-o"
        ><span>我的</span>
        <template #icon>
          <img
            :src="
              active == 4
                ? 'data:image/png;base64,'
                : 'data:image/png;base64,'
            "
            alt=""
          /> </template
      ></van-tabbar-item>
    </van-tabbar>
  </div>
</template>


<script>
export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      active: JSON.parse(sessionStorage.getItem("active")) || 0,
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    //   底部切换
    act() {
      console.log();
      //   保存本地
      sessionStorage.setItem("active", this.active);
    },
  },
  created() {},
  mounted() {},
};
</script>


<style lang="scss" scoped>
.van-tabbar-item__icon img{
  height: 0.36rem;
}
</style>
